$font-1: "font-1", sans-serif;

$color-1 : #61dafb;
$color-2: salmon ;
$color-3 :#282c34;
$white: rgb(243, 243, 243);

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background: $white;
  font-family: $font-1;
}

li{
  list-style-type: none;
}